import React, { useEffect, useState } from 'react';
import { Button } from 'react-bootstrap';
import {useHistory,useParams} from 'react-router-dom';
import Alert from '../Common/Alert';
import './Detail.scss';

function Detail(props){
    
    let [ alert, setAlert ] = useState(true);
    let history = useHistory();    
    let { id } = useParams();
    //React Hook의 사용 예
    useEffect(()=>{

        let alertTimer = setTimeout(()=>{
            setAlert(false);
            
        },2000);
    return ()=>{
        clearTimeout(alertTimer);
    }
    },[alert]);

    // Component가 Mount 되었을 때 
    // Component가 update 될 때
    // 특정 코드를 실행 할 수 있다.
    let findProduct = props.data.find((prod)=>{
        return prod.id.toString() === id;
    });
    let orderBtn = (()=>{

    });


    return(
        <div className="container">
            <div>
                <h1 className="red">{findProduct.title}</h1>
            </div>

            {
                alert === true 
                ? (<Alert information={'재고가 얼마 남지 않았습니다.'}/>) 
                : null
            }
            

            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${findProduct.id + 1}.jpg`} width="100%" alt="shoes"/>
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{findProduct.title}</h4>
                    <p>{findProduct.content}</p>
                    <p>{findProduct.price}원</p>
                    <InfoStock stock={props.stock} findProduct = {findProduct}/>
                    <button onClick={orderBtn} className="btn btn-danger">주문하기</button>
                    <Button variant="warning" onClick={()=>{
                        history.goBack();
                    }}>뒤로가기</Button> 
                </div>
            </div>
        </div>
    )
}

function InfoStock(props){
    let stockCount = props.stock[props.findProduct.id];

    return (<div className='stock'> 재고 : {stockCount} 개</div>)
}

export default Detail;